import router from '@/router/index'
import axios from 'axios'
axios.defaults.timeout = 5000

//  请求拦截
axios.interceptors.request.use(config => {
  // if (store.state.token) {  // 判断是否存在token，如果存在的话，则每个http header都加上token
  //   config.headers.Authorization = `token ${store.state.token}`;
  // }
  return config
}, error => {
  return Promise.reject(error)
})

//  响应拦截
axios.interceptors.response.use(data => {
  var res = JSON.parse(data.request.response)
  if (res.code === 50001) {
    router.replace({path: '/', query: {redirect: router.currentRoute.fullPath}})
  } else {
    return res
  }
}, error => {
  return Promise.reject(error)
})
// 客服登录
export function login (username, passoword) {
  return axios.get('/api/login', {params: {
    username: username,
    password: passoword
  }})
}
// 客服是否登录
export function islogin () {
  return axios.get('/api/islogin')
}
// 获取留言列表
export function getmessages () {
  return axios.get('/api/getmessages')
}
// 获取留言详情
export function getmessage (id) {
  return axios.get('/api/getmessage', {params: {id: id}})
}

// 用户登录
export function userlogin () {
  return axios.get('/api/userlogin')
}
// 用户提交留言
export function submessage (from, name, mobile, email, wechat, message) {
  return axios.post('/api/submessage', {
    from: from,
    name: name,
    mobile: mobile,
    email: email,
    wechat: wechat,
    message: message
  })
}



