import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const state = {
  username: '',
  uid: '',
  name: '',
  type: ''
}

const getters = {
  username: state => state.username,
  uid: state => state.uid,
  name: state => state.name,
  type: state => state.type
}

const mutations = {
  SET_USERNAME (state, change) {
    state.username = change
  },
  SET_UID (state, change) {
    state.uid = change
  },
  SET_NAME (state, change) {
    state.name = change
  },
  SET_TYPE (state, change) {
    state.type = change
  }
}

export default new Vuex.Store({
  state,
  getters,
  mutations
})



