<template>
	<div>
		<h1>留言</h1>
		<div class="page">
      <h1>你好，现在是非工作时间</h1>
      <h1>请填写联系方式以及留言，我们稍后给您回复</h1>
      <div class="formgroup">
        <label>姓名</label>
        <input type="text" class="form" v-model='name'>
      </div>
      <div class="formgroup">
        <label>电话</label>
        <input type="text" class="form" maxlength=11 v-model='mobile'>
      </div>
      <div class="formgroup">
        <label>邮箱</label>
        <input type="email" class="form" v-model='email'>
      </div>
      <div class="formgroup">
        <label>微信</label>
        <input type="text" class="form" v-model='wechat'>
      </div>
      <div class="formgroup">
        <label>留言</label>
        <textarea type="text" class="text" v-model='message'></textarea>
      </div>
      <div class="formgroup">
        <button type="button" class="subbtn btn btn-primary" @click='sub'>提交</button>
      </div>
		</div>
	</div>
</template>
<script>
import {submessage} from '@/api.js'

export default {
  name: 'usermessage',
  data () {
    return {
      name: '',
      mobile: '',
      email: '',
      wechat: '',
      message: ''
    }
  },
  mounted () {
  },
  methods: {
    sub () {
      submessage(this.$store.state.username, this.name, this.mobile, this.email, this.wechat, this.message).then(res => {
        alert(res.msg)
        this.name = ''
        this.mobile = ''
        this.email = ''
        this.wechat = ''
        this.message = ''
      })
    }
  }
}
</script>
<style scope>
.formgroup{
  width: 60%;
  margin: 15px auto;
}
.form{
  width: 80%;
  height: 34px;
  padding: 6px 12px;
  line-height: 34px;
  color: #555;
  background-color: #fff;
  border: 1px solid #ccc;
  transition: border-color ease-in-out .15s,box-shadow ease-in-out .15s
}
.lab{
  display: inline-block;
}
.text{width: 80%;
  height: 150px;
  padding: 6px 12px;
  line-height: 20px;
  vertical-align: top;
  color: #555;
  resize: none;
  background-color: #fff;
  border: 1px solid #ccc;
  transition: border-color ease-in-out .15s,box-shadow ease-in-out .15s
}
.subbtn{
  float: right;
  margin-right: 28px;
}
</style>



