<template>
	<div>
		<h1>客服{{user.name}}</h1>
		<div class="page">
			<div class="chatArea">
				<ul class="messages">
					<li v-for='x in messages' v-bind:class="x.class">
						<div v-if='x.class==="log"'>{{x.msg}}</div>
						<div v-if='x.class==="message"'>
							<div class='time'>{{x.time}}</div>
							<div v-bind:class="x.type=='get'?'left':'right'">{{x.name}}</div><br>
							<div v-bind:class="x.type=='get'?'leftmsg':'rightmsg'">{{x.msg}}</div>
						</div>
					</li>
				</ul>
			</div>
				<input v-model="inputMessage" class="inputMessage" placeholder="Type here..." @keyup.enter="sendMessage"/>
				<input type="submit" style="display:none;">
		</div>
	</div>
</template>
<script>
import io from '../../static/js/socket.io'

export default {
  name: 'kefuchat',
  data () {
    return {
      messages: [{class: 'log', msg: '正在连接服务器...'}],
      inputMessage: '',
      linked: 0,
      connected: 0,
      name: ''
    }
  },
  mounted () {
    this.connect()
  },
  computed: {
    user () {
      return this.$store.state
    }
  },
  methods: {
    connect () {
      this.socket = io('10.0.0.39:3000')
      var _this = this
      _this.socket.on('connect', function () {
        _this.connected = 1
        _this.socket.emit('login', {uid: _this.user.uid, type: _this.user.type, name: _this.user.name})
      })
      _this.socket.on('log', function (msg) {
        _this.log({class: 'log', msg: msg})
      })
      _this.socket.on('linked', function (name) {
        _this.linked = 1
        _this.log({class: 'log', msg: '与用户关联成功!'})
      })
      _this.socket.on('error', function () {
        _this.linked = _this.connected = 0
        _this.log({class: 'log', msg: '连接已断开...'})
      })
      _this.socket.on('new message', function (obj) {
        _this.addChatMessage({class: 'message', name: obj.username, type: 'get', time: _this.formatDate(new Date()), msg: obj.message})
      })
    },
    log (str) {
      this.messages.push(str)
    },
    addChatMessage (obj) {
      this.messages.push({class: obj.class, name: obj.name, type: obj.type, time: obj.time, msg: obj.msg})
    },
    sendMessage () {
      if (this.linked) {
        let message = this.inputMessage
        this.inputMessage = ''
        this.addChatMessage({class: 'message', name: '我', type: 'send', time: this.formatDate(new Date()), msg: message})
        this.socket.emit('new message', {id: '1234', message: message})
      } else {
        this.log({class: 'log', msg: '还未连接游客，不能发送消息'})
      }
    },
    formatDate (t) {
      var M = t.getMonth() + 1
      var D = t.getDate()
      var H = t.getHours()
      var m = t.getMinutes()
      var s = t.getSeconds()
      if (M < 10) {
        M = '0' + M
      }
      if (D < 10) {
        D = '0' + D
      }
      if (H < 10) {
        H = '0' + H
      }
      if (m < 10) {
        m = '0' + m
      }
      if (s < 10) {
        s = '0' + s
      }
      return [M, '-', D, ' ', H, ':', m, ':', s].join('')
    }
  }
}
</script>
<style src="../../static/css/style.css"></style>
<style scoped>
	.time{
		margin: 0 auto;
		text-align: center;
	}
	.left{
		float: left;
		color: green;
	}
	.leftmsg{
		margin-left: 10px;
		float: left;
		color: green;
	}
	.right{
		float: right;
		color: red;
	}
	.rightmsg{
		margin-right: 10px;
		float: right;
		color: red;
	}
</style>



