<template>
	<div>
		<h1>微信留言</h1>
		<div class="page">
			<ul class="messages">
				<li v-for='(message, index) in messages' class="msgcontent">
					<div class="word">{{message.from}}的留言</div>
					<div class="word">{{message.time | timeFormat}}</div>
					<div class="look" @click='showdetil(index, message._id)'>查看</div>
					<message-details :details="details" class="details" :class='{active: deactive === index}'></message-details>
				</li>
			</ul>
		</div>
	</div>
</template>
<script>
import {getmessages, getmessage} from '@/api.js'
import messageDetails from '@/components/messagedetails'
export default {
  name: 'kefumessage',
  props: ['update'],
  data () {
    return {
      messages: [],
      deactive: -1,
      details: {}
    }
  },
  components: {messageDetails},
  watch: {
    update (n, o) {
      if (n === 2) {
        this.getMessages()
      }
    }
  },
  filters: {
    timeFormat (value) {
      var t = new Date(parseInt(value))
      var M = t.getMonth() + 1
      var D = t.getDate()
      var H = t.getHours()
      var m = t.getMinutes()
      var s = t.getSeconds()
      if (M < 10) {
        M = '0' + M
      }
      if (D < 10) {
        D = '0' + D
      }
      if (H < 10) {
        H = '0' + H
      }
      if (m < 10) {
        m = '0' + m
      }
      if (s < 10) {
        s = '0' + s
      }
      return [M, '-', D, ' ', H, ':', m, ':', s].join('')
    }
  },
  mounted () {
  },
  computed: {
  },
  methods: {
    getMessages () {
      this.messages = []
      getmessages().then(res => {
        if (res.code === 200) {
          res.msg.map(v => {
            this.messages.push(v)
          })
        }
      })
    },
    showdetil (index, id) {
      if (this.deactive === index) {
        this.deactive = -1
      } else {
        getmessage(id).then(res => {
          if (res.code === 200) {
            this.details = res.msg
            this.deactive = index
          }
        })
      }
    }
  }
}
</script>
<style scoped>
	.msgcontent{
		margin-bottom: 10px;
		overflow: hidden;
		border-bottom: 1px solid #ccc;
	}
	.word{
		margin-left: 10px;
		float: left;
	}
	.look{
		margin-right: 20px;
		float: right;
		cursor: pointer;
	}
	.details{
		height: 0px;
		margin-top: 20px;
		transition: height .5s;
	}
	.details.active{
		height: 165px;
	}
</style>



