import Vue from 'vue'
import Router from 'vue-router'
import {islogin} from '@/api'
import store from '@/vuex/store'

import Login from '@/pages/login'
import Kefu from '@/pages/kefu'
import User from '@/pages/user'

Vue.config.debug = true
Vue.use(Router)

var router = new Router({
  mode: 'history',
  base: __dirname,
  routes: [
    {
      path: '/',
      name: 'Login',
      meta: {
        title: '在线客服系统登录'
      },
      component: Login
    },
    {
      path: '/kefu',
      name: 'Kefu',
      meta: {
        requireAuth: true,
        title: '客服'
      },
      component: Kefu
    },
    {
      path: '/user',
      name: 'User',
      meta: {
        title: '用户'
      },
      component: User
    }
  ]
})

router.beforeEach((to, from, next) => {
  if (to.meta.requireAuth) {
    islogin().then(res => {
      if (res.code === 200) {
        store.commit('SET_USERNAME', res.msg.username)
        store.commit('SET_UID', res.msg.uid)
        store.commit('SET_NAME', res.msg.name)
        store.commit('SET_TYPE', res.msg.type)
        return next()
      } else {
        return next({path: '/', query: {redirect: to.fullPath}})
      }
    })
  } else {
    next()
  }
})

router.afterEach((to, from, next) => {
  document.title = to.meta.title
})

export default router



