<template>
  <div class="register-box">
    <div class="register-box-body">
      <p class="login-box-msg" style="font-size:20px;padding:0 10px 10px 10px;font-weight:bold;">
        <img src="/static/img/u6.jpg" style="width:120px;height:60px">
      </p>
      <div class="form-group has-feedback">
        <div class="col-xs-3"></div>
        <div class="col-xs-6" style="text-align:center;height:20px;">{{message}}</div>
      </div>
      <form>
        <div class="form-group has-feedback">
          <input type="text" class="form-control" name="account" maxlength="11" placeholder="请输入用户名" v-model="username">
          <span class="glyphicon glyphicon-phone form-control-feedback"></span>
        </div>
        <div class="form-group has-feedback">
          <input type="password" class="form-control" name="password"  placeholder="请输入密码" v-model="passoword">
          <span class="glyphicon glyphicon-phone form-control-feedback"></span>
        </div>
      </form>

      <div class="row" style="margin-top:10px">
        <!-- /.col -->
        <div class="col-xs-12">
          <button class="btn btn-primary btn-block btn-flat" v-on:click="login">登录</button>
          <!-- <button class="btn btn-primary btn-block btn-flat" v-on:click="register">注册</button> -->
        </div>
        <!-- /.col -->
      </div>

    </div>
  </div>
</template>
<script>
import {login} from '@/api.js'

export default {
  name: 'login',
  data () {
    return {
      username: '',
      passoword: '',
      message: ''
    }
  },
  methods: {
    login: function () {
      login(this.username, this.passoword).then(res => {
        if (res.code === 200) {
          this.$store.commit('SET_USERNAME', this.username)
          this.$store.commit('SET_UID', res.msg.uid)
          this.$store.commit('SET_TYPE', res.msg.name)
          window.sessionStorage['username'] = this.username
          window.sessionStorage['uid'] = res.msg.uid
          window.sessionStorage['name'] = res.msg.name
          this.$router.push({ path: '/kefu' })
        } else {
          this.message = res.msg
        }
      })
    }
  }
}
</script>



