<template>
	<div class="content">
		<ul class='menu'>
			<li v-for='(tab, index) in  tabs' class='tabs' :class="{active: isactive === index}" @click='changetab(index)'>{{tab.text}}</li>
		</ul>
		<div class="tab">
			<kefu-chat class="tab-content" :class="{active: isactive === 0}"></kefu-chat>
			<kefu-message class="tab-content" :class="{active: isactive === 1}" :update="isactive"></kefu-message>
			<kefu-wechat class="tab-content" :class="{active: isactive === 2}" :update="isactive"></kefu-wechat>
		</div>
	</div>
</template>
<script>
import kefuChat from '@/components/kefuchat'
import kefuMessage from '@/components/kefumessage'
import kefuWechat from '@/components/wechat'

export default {
  name: 'kefu',
  data () {
    return {
      tabs: [
        {text: '聊天'},
        {text: 'web留言'},
        {text: '微信留言'}
      ],
      isactive: 0
    }
  },
  components: {
    kefuChat,
    kefuMessage,
    kefuWechat
  },
  mounted () {
  },
  computed: {
  },
  methods: {
    changetab (index) {
      this.isactive = index
    }
  }
}
</script>
<style>
	.content {
	  font-size: 12px;
	  width: 600px;
	  height: 600px;
	  margin:50px auto;
	  padding: 0px;
	  background: #eee;
	  overflow:hidden;
	}
	.menu{
		width: 100px;
		height: 100%;
		float: left;
		padding-left: 0px;
		text-align: center;
		background: #222d32;
	}
	.tabs{
		height: 40px;
		line-height: 40px;
		cursor: pointer;
		color: white;
	}
	.tabs:hover{
		background: black;
	}
	.tabs.active{
		background: black;
	}
	.tab{
		width: 500px;
		height: 100%;
		float: left;
	}
	.tab-content{
		width: 100%;
		height: 100%;
		display: none;
	}
	.tab-content.active{
		display: block;
	}
</style>



