<template>
	<div>
    <div class="formgroup">
      <label>姓名</label>
      <input type="text" class="form" disabled="" :value="details.name">
      <label>电话</label>
      <input type="text" class="form" disabled="" maxlength=11 :value="details.mobile">
    </div>
    <div class="formgroup">
      <label>邮箱</label>
      <input type="email" class="form" disabled="" :value="details.email">
      <label>微信</label>
      <input type="text" class="form" disabled="" :value="details.wechat">
    </div>
    <div class="formgroup">
      <label>留言</label>
      <textarea type="text" class="text" disabled="">{{details.message}}</textarea>
    </div>
	</div>
</template>
<script>

export default {
  name: 'kefumessage',
  props: [
    'details'
  ],
  data () {
    return {
    }
  },
  mounted () {
  },
  computed: {
  },
  methods: {
  }
}
</script>
<style scoped>
.formgroup{
  margin: 5px auto;
}
.form{
  height: 34px;
  padding: 6px 12px;
  line-height: 34px;
  color: #555;
  background-color: #fff;
  border: 1px solid #ccc;
  transition: border-color ease-in-out .15s,box-shadow ease-in-out .15s
}
.lab{
  display: inline-block;
}
.text{width: 93%;
  height: 80px;
  padding: 6px 12px;
  line-height: 20px;
  vertical-align: top;
  color: #555;
  resize: none;
  background-color: #fff;
  border: 1px solid #ccc;
  transition: border-color ease-in-out .15s,box-shadow ease-in-out .15s
}
</style>



