<template>
	<div class="content">
		<ul class='menu'>
			<li v-for='(tab, index) in  tabs' class='tabs' :class="{active: isactive === index}" @click='changetab(index)'>{{tab.text}}</li>
		</ul>
		<div class="tab">
			<user-chat :user='user' class="tab-content" :class="{active: isactive === 0}"></user-chat>
			<user-message class="tab-content" :class="{active: isactive === 1}"></user-message>
		</div>
	</div>
</template>
<script>
import {userlogin} from '@/api'

import userChat from '@/components/userchat'
import userMessage from '@/components/usermessage'

export default {
  name: 'user',
  data () {
    return {
      tabs: [
        {text: '聊天'},
        {text: '留言'}
      ],
      isactive: 0,
      user: {}
    }
  },
  components: {
    userChat,
    userMessage
  },
  created () {
    userlogin().then(res => {
      if (res.code === 200) {
        this.$store.commit('SET_NAME', res.msg.name)
        this.$store.commit('SET_UID', res.msg.uid)
        this.$store.commit('SET_TYPE', res.msg.type)
        this.user = {
          username: res.msg.username,
          uid: res.msg.uid,
          name: res.msg.name,
          type: res.msg.type
        }
      } else {
        console.log('错误')
      }
    })
  },
  mounted () {
  },
  methods: {
    changetab (index) {
      this.isactive = index
    }
  }
}
</script>
<style>
.content {
  font-size: 12px;
  width: 600px;
  height: 600px;
  margin:50px auto;
  padding: 0px;
  background: #eee;
  overflow:hidden;
}
.menu{
	width: 100px;
	height: 100%;
	float: left;
	padding-left: 0px;
	text-align: center;
	background: #222d32;
}
.tabs{
	height: 40px;
	line-height: 40px;
	cursor: pointer;
	color: white;
}
.tabs:hover{
	background: black;
}
.tabs.active{
	background: black;
}
.tab{
	width: 500px;
	height: 100%;
	float: left;
}
.tab-content{
	width: 100%;
	height: 100%;
	display: none;
}
.tab-content.active{
	display: block;
}
</style>



